<template>
  <div>
    <div class="sm-form ">
        <label for="username">Your username</label>
        <input type="text" id="username" name="username" class="form-control form-control-sm" placeholder="username" v-model="vdata.username" >
    </div>
    <div class="sm-form ">
        <label for="password">Your password</label>
        <input type="password" id="password" name="password" class="form-control form-control-sm" placeholder="password" v-model="vdata.password" >
    </div>
    <button type="button" @click='register' class="btn btn-sm btn-primary  ">register</button>
  </div>
</template>
<script>
import autophp from '@/plugins/autophp';
let sdb = new autophp();
export default{
  data(){
    return{
      vdata:{}
    }
  },
  methods: {
    register(){
      sdb.collection('tbuser').register(this.vdata).then(res=>{
           console.log(res)
       })
    },
  },
}
</script>